<template>
  <div>
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <div class="title">
          <b>{{ article.title }}</b>
          <div>
            <b>作者：</b>{{ article.teacher }}
            <b>时间：</b>{{ article.time }}
            <b>售价：</b>{{ article.price }}
          </div>
        </div>
      </div>
      <div>
        <div class="introduce">
          <b>介绍：</b>{{ article.remark }}
        </div>
        <share :config="config" data-disabled="google,twitter,facebook,linkedin,douban,tencent,diandian" data-qq-title="好的源码，大家分享"></share>
        <div v-for="(o) in article.images" :key="o">
          <img :src="o"/>
        </div>
      </div>
      <div>
        <p v-if="proArticle != null">上一篇：
          <router-link :to="'/article?id=' + proArticle.id">{{proArticle.title}}</router-link>
        </p>
        <p v-if="nextArticle != null">下一篇：
          <router-link :to="'/article?id=' + nextArticle.id">{{nextArticle.title}}</router-link>
        </p>
      </div>
    </el-card>
  </div>
</template>

<script>
import { tjzp } from '../../../static/js/article.js'

export default {
  data () {
    return {
      id: this.$route.query.id,
      tjzp: tjzp,
      article: tjzp.data[this.$route.query.id - 1],
      proArticle: tjzp.data[this.$route.query.id - 2],
      nextArticle: tjzp.data[this.$route.query.id]
    }
  }
}
</script>

<style scoped>
img {
  width: 100%;
}
.title {
  font-size: 20px;
}
.title div {
  font-size: 15px;
  color: red;
  margin-top: 10px;
}
.introduce {
  padding: 10px;
  background-color: #EFF2F7;
  margin-bottom: 10px;
  border: solid 1px #F3F3F3;
  line-height: 23px;
}
</style>



